            display: flex ;
            align-items: center ;
            justify-content: center ;
            width: 100%;
        } 

            .boton_buscador{
                cursor: pointer ;
                position: relative ;
                touch-action: manipulation ;
                font-family: inherit ;
                font-size: inherit ;
                line-height: inherit ;
                font-weight: inherit ;
                border-radius: 0px ;
                outline: none ;
                transition: box-shadow 0.2s ease 0s, transform 0.1s ease 0s;
                background: transparent ;
                border: none ;
                color: inherit ;
                display: block ;
                margin: 0px ;
                padding: 0px ;
                text-align: inherit ;
                text-decoration: none ;
                height: 100% ;
            }

            input.input_buscador{
                font-size:17px;
                line-height: 20px;
                font-weight: 400;
                background: none;
                border: 0px ;
                color: rgb(34, 34, 34) ;
                margin:0px ;
                padding:15px;
                width: 100%;
                text-overflow: ellipsis;
            }

                .input_buscador:focus-visible{
                    outline: none;
                }

